import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { useParams } from "react-router";
import artistActions from "../redux/actions/artistActions";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import CircularProgress from "@material-ui/core/CircularProgress";
import CloseIcon from "@material-ui/icons/Close";

function ArtistDetails(props) {
  const params = useParams();
  const [open, setOpen] = useState(false);
  const [selected, setSelected] = useState({});
  const handleOpen = (data) => {
    setSelected(data);
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  useEffect(() => {
    props.getSingleArtist({ artist_id: params.artistId });
    props.setIsGoBack(true);
  }, []);
  return (
    <>
      {props.getArtistLoading ? (
        <div
          style={{
            width: "100%",
            height: "100%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <CircularProgress size="80px" style={{ color: "#663300" }} />
        </div>
      ) : (
        <div
          style={{
            width: "100%",
            height: "100%",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <div style={{ padding: "5rem 3rem 2rem" }}>
            <img
              src={props?.getArtistData?.artist_image}
              alt={props?.getArtistData?.artist_name}
              style={{ height: "300px", objectFit: "contain" }}
            />
          </div>
          <div
            style={{
              marginBottom: "2rem",
              fontSize: "38px",
              fontWeight: "bold",
            }}
          >
            {props?.getArtistData?.artist_name_ar}
          </div>
          <div
            style={{
              marginBottom: "2rem",
              fontSize: "28px",
              width: 700,
              textAlign: "end",
              whiteSpace: "pre-wrap",
            }}
          >
            {props?.getArtistData?.artist_description_ar}
          </div>
          <div
            style={{
              marginBottom: "2rem",
              fontSize: "38px",
              fontWeight: "bold",
            }}
          >
            {props?.getArtistData?.artist_name}
          </div>
          <div
            style={{
              marginBottom: "4rem",
              fontSize: "28px",
              width: 700,
              textAlign: "start",
              whiteSpace: "pre-wrap",
            }}
          >
            {props?.getArtistData?.artist_description}
          </div>
          <div
            style={{ display: "flex", width: "70%", justifyContent: "center" }}
          >
            {props?.getArtistData?.artist_arts?.map((zone, i) => {
              return (
                <div key={i} style={{ margin: "0 2rem", width: "300px" }}>
                  <img
                    src={zone.art_image}
                    alt="art"
                    onClick={() => handleOpen(zone)}
                    style={{
                      width: "300px",
                      height: "300px",
                      objectFit: "cover",
                    }}
                  />
                  <div style={{ fontSize: "22px" }}>{zone.art_name}</div>
                </div>
              );
            })}
          </div>
        </div>
      )}
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        open={open}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div
            style={{
              overflow: "auto",
              outline: "0",
              width: "100%",
              height: "100%",
              backgroundColor: "white",
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              position: "relative",
            }}
          >
            <div style={{ padding: "5rem 3rem 2rem" }}>
              <img
                src={selected?.art_image}
                alt={selected?.art_name}
                style={{ height: "700px", objectFit: "contain" }}
              />
            </div>
            <div
              style={{
                marginBottom: "2rem",
                fontSize: "38px",
                fontWeight: "bold",
              }}
            >
              {selected?.art_name}
            </div>
            <div
              style={{
                fontSize: "28px",
                padding: "0 3rem",
                alignSelf: "start",
              }}
            >
              <div>
                <strong>Medium: </strong>
                {selected?.art_medium}
              </div>
              {selected?.art_year !== "N/A" && (
                <div>
                  <strong>Year: </strong>
                  {selected?.art_year}
                </div>
              )}
              <div>
                <strong>Dimensions: </strong>
                {selected?.art_dimensions}
              </div>
              <div>
                <strong>Origin: </strong>
                {selected?.art_origin}
              </div>
              {selected?.art_description && (
                <>
                  <div
                    style={{ margin: "4rem 0 6rem", whiteSpace: "pre-wrap" }}
                  >
                    {selected?.art_description}
                  </div>
                </>
              )}
              {selected?.art_description_ar && (
                <div
                  style={{
                    margin: "4rem 0 6rem",
                    textAlign: "end",
                    whiteSpace: "pre-wrap",
                  }}
                >
                  {selected?.art_description_ar}
                </div>
              )}
            </div>
            <div
              style={{ position: "absolute", top: "1rem", right: "1rem" }}
              onClick={handleClose}
            >
              <CloseIcon style={{ fontSize: "40px", color: "#663300" }} />
            </div>
          </div>
        </Fade>
      </Modal>
    </>
  );
}

const mapStateToProps = (state) => ({
  getArtistData: state.artist.getArtistData,
  getArtistLoading: state.artist.getArtistLoading,
});

const mapDispatchToProps = (dispatch) => ({
  getSingleArtist: (data) => {
    dispatch(artistActions.getSingleArtist(data));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(ArtistDetails);
