import { CircularProgress, Grid } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import ArtCard from "../components/common/ArtCard";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import { connect } from "react-redux";
import artActions from "../redux/actions/artActions";
import { useHistory } from "react-router";
import CloseIcon from "@material-ui/icons/Close";

function Arts(props) {
  const history = useHistory();
  const [open, setOpen] = useState(false);
  const [selected, setSelected] = useState({});
  const handleOpen = (data) => {
    setSelected(data);
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  useEffect(() => {
    props.getAllArts();
    props.setIsGoBack(true);
  }, []);
  return (
    <>
      {props.getAllArtsLoading ? (
        <div
          style={{
            width: "100%",
            height: "100%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <CircularProgress size="80px" style={{ color: "#663300" }} />
        </div>
      ) : (
        <Grid container style={{ position: "relative" }}>
          {props.getAllArtsData?.length > 0 &&
            props.getAllArtsData?.map((zone, i) => {
              return (
                <Grid key={`art-${i}`} item xs={6} style={{}}>
                  <ArtCard
                    image={zone.art_image}
                    artName={zone.art_name}
                    artistName={zone.artist?.artist_name}
                    zone={zone}
                    handleOpen={handleOpen}
                  />
                </Grid>
              );
            })}
          <Modal
            aria-labelledby="transition-modal-title"
            aria-describedby="transition-modal-description"
            open={open}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
              timeout: 500,
            }}
          >
            <Fade in={open}>
              <div
                style={{
                  overflow: "auto",
                  outline: "0",
                  width: "100%",
                  height: "100%",
                  backgroundColor: "white",
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  position: "relative",
                }}
              >
                <div style={{ padding: "5rem 3rem 2rem" }}>
                  <img
                    src={selected?.art_image}
                    alt={selected?.art_name}
                    style={{ height: "700px", objectFit: "contain" }}
                  />
                </div>
                <div
                  style={{
                    marginBottom: "2rem",
                    fontSize: "38px",
                    fontWeight: "bold",
                  }}
                >
                  {selected?.art_name}
                </div>
                <div
                  style={{
                    fontSize: "28px",
                    padding: "0 3rem",
                    alignSelf: "start",
                  }}
                >
                  <div>
                    <strong>Medium: </strong>
                    {selected?.art_medium}
                  </div>
                  {selected.art_year !== "N/A" && (
                    <div>
                      <strong>Year: </strong>
                      {selected?.art_year}
                    </div>
                  )}
                  <div>
                    <strong>Dimensions: </strong>
                    {selected?.art_dimensions}
                  </div>
                  <div>
                    <strong>Origin: </strong>
                    {selected?.art_origin}
                  </div>
                  {selected?.art_description && (
                    <div
                      style={{ margin: "4rem 0 6rem", whiteSpace: "pre-wrap" }}
                    >
                      {selected?.art_description}
                    </div>
                  )}
                  {selected?.art_description_ar && (
                    <div
                      style={{
                        margin: "4rem 0 6rem",
                        textAlign: "end",
                        whiteSpace: "pre-wrap",
                      }}
                    >
                      {selected?.art_description_ar}
                    </div>
                  )}
                </div>
                <div style={{ marginBottom: "2rem" }}>
                  <img
                    src={selected?.artist?.artist_image}
                    alt=""
                    style={{
                      width: "300px",
                      height: "300px",
                      objectFit: "cover",
                    }}
                    onClick={() => {
                      history.push(`/artist/${selected?.artist?._id}`);
                    }}
                  />
                </div>
                <div style={{ marginBottom: "2rem", fontSize: "28px" }}>
                  {selected?.artist?.artist_name}
                </div>
                <div
                  style={{ position: "absolute", top: "1rem", right: "1rem" }}
                  onClick={handleClose}
                >
                  <CloseIcon style={{ fontSize: "40px", color: "#663300" }} />
                </div>
              </div>
            </Fade>
          </Modal>
        </Grid>
      )}
    </>
  );
}

const mapStateToProps = (state) => ({
  getAllArtsData: state.art.getAllArtsData,
  getAllArtsLoading: state.art.getAllArtsLoading,
});

const mapDispatchToProps = (dispatch) => ({
  getAllArts: () => {
    dispatch(artActions.getAllArts());
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(Arts);
