import React from "react";

function ArtCard(props) {
  return (
    <div style={{ padding: "2rem", fontSize: "28px" }}>
      <div>
        <img
          src={props.image}
          alt="ArtImage"
          style={{ objectFit: "cover", width: "100%", height: "400px" }}
          onClick={() => {
            props.handleOpen(props.zone);
          }}
        />
      </div>
      <div style={{ fontSize: "32px" }}>{props.artName}</div>
      <div>By {props.artistName}</div>
    </div>
  );
}

export default ArtCard;
