import React, { useEffect } from "react";
import { useHistory } from "react-router";

// IMAGES
import Logo from "../images/Khawla Art & Culture logo.jpg";
import QR from "../images/QR.png";

function Home(props) {
  const history = useHistory();
  useEffect(() => {
    props.setIsOpen(false);
    props.setIsGoBack(false);
  }, []);
  return (
    <div
      onClick={() => {
        history.push("/menu");
        props.setIsOpen(true);
      }}
      style={{
        width: "100vw",
        height: "100vh",
        display: "flex",
        // justifyContent: "center",
        alignItems: "center",
        flexDirection: "column",
      }}
    >
      <img src={Logo} alt="Khawla Art & Culture Logo" width="100%" />
      <div>
        <div
          style={{
            fontSize: "105px",
            color: "#663300",
            fontFamily: "GilroyBold",
          }}
        >
          الوحدة في التنوع
        </div>
        <div
          style={{
            fontSize: "72px",
            color: "#663300",
            fontFamily: "GilroyBold",
            margin: "1rem 0 5rem",
          }}
        >
          Unity in Diversity
        </div>
      </div>
      <div
        style={{ fontSize: "32px", color: "#663300", margin: "1rem 0 5rem" }}
      >
        28 September - 12 October, 2021
      </div>
      <div>
        <img src={QR} alt="qr" />
      </div>
    </div>
  );
}

export default Home;
