import React, { useEffect, useState } from "react";
import { useHistory } from "react-router";
import Logo from "../../images/khawla_logo_onscroll.png";
import KeyboardBackspaceIcon from "@material-ui/icons/KeyboardBackspace";

function Header(props) {
  const history = useHistory();
  return (
    <div
      style={{
        top: 0,
        left: 0,
        right: 0,
        height: props.isOpen ? "10vh" : "0vh",
        backgroundColor: "white",
        borderBottom: "1px solid #663300",
        color: "white",
        position: "fixed",
        transition: "1000ms all",
        zIndex: "3",
      }}
    >
      {props.isGoBack && (
        <div
          style={{
            color: "black",
            position: "absolute",
            top: "50%",
            left: "2rem",
            transform: "translateY(-50%)",
          }}
          onClick={() => {
            // console.log(/^\/artist|^\/arts?/.test(history.location.pathname));
            history.goBack();
          }}
        >
          <KeyboardBackspaceIcon
            style={{ cursor: "pointer", color: "#663300", fontSize: "40px" }}
          />
        </div>
      )}
      <div
        style={{
          position: "absolute",
          top: "50%",
          left: "50%",
          transform: "translate(-50%,-50%)",
        }}
        onClick={() => {
          history.push("/");
        }}
      >
        {props.isOpen && (
          <img
            src={Logo}
            alt="logo"
            style={{ objectFit: "contain", width: "300px", cursor: "pointer" }}
          />
        )}
      </div>
    </div>
  );
}

export default Header;
